import React from "react";
import QueueAnim from "rc-queue-anim";
import OverPack from "rc-scroll-anim/lib/ScrollOverPack";
import { Row, Col } from "antd";
import "./trustby.scss";

const base = 0;
const page1 = [
    {
        img: `landing-page/OpenZappellin.svg`,
        href: `${base}zh-cn/g2/3.x/index.html`,
        title: "G2",
        description:
            " 是以数据为驱动，具有高度的易用性和扩展性的可视化图形语法。",
    },
    {
        img: `landing-page/ORACLE.svg`,
        href: `${base}zh-cn/g2/3.x/index.html`,
        title: "G2",
        description:
            " 是以数据为驱动，具有高度的易用性和扩展性的可视化图形语法。",
    },
    {
        img: `landing-page/MORPHEUS.svg`,
        href: `${base}zh-cn/g2/3.x/index.html`,
        title: "G2",
        description:
            " 是以数据为驱动，具有高度的易用性和扩展性的可视化图形语法。",
    },
    {
        img: `landing-page/SAMSUNG.svg`,
        href: `${base}zh-cn/g2/3.x/index.html`,
        title: "G2",
        description:
            " 是以数据为驱动，具有高度的易用性和扩展性的可视化图形语法。",
    },
    {
        img: `landing-page/monday.svg`,
        href: `${base}zh-cn/g2/3.x/index.html`,
        title: "G2",
        description:
            " 是以数据为驱动，具有高度的易用性和扩展性的可视化图形语法。",
    },
    {
        img: `landing-page/segment.svg`,
        href: `${base}zh-cn/g2/3.x/index.html`,
        title: "G2",
        description:
            " 是以数据为驱动，具有高度的易用性和扩展性的可视化图形语法。",
    },
    {
        img: `landing-page/Protonet.svg`,
        href: `${base}zh-cn/g2/3.x/index.html`,
        title: "G2",
        description:
            " 是以数据为驱动，具有高度的易用性和扩展性的可视化图形语法。",
    },
];

export default function Page1() {
    const children = page1.map((card, i) => (
        <Col className="card-wrapper" key={i.toString()} md={3} xs={7}>
            <img src={`${card.img}`} className="h-[30px]" alt="trust" />
        </Col>
    ));

    return (
        <OverPack component="section" className={`page-wrapper min-h-[400px]`}>
            <QueueAnim
                component={Row}
                type="bottom"
                className="page row text-center page-trust-by"
                leaveReverse={true}
                key="trust-by-what"
            >
                <Col
                    span={24}
                    className="trustby-heading mb-10"
                    key="trustby-heading"
                >
                    Trusted by the best in B2B
                </Col>
                <Col
                    span={24}
                    key="trust-companies"
                    className="flex justify-between"
                >
                    <Row className="page" justify={"space-between"}>
                        {/* <QueueAnim
                        component={props => <Row type='flex' justify='space-between' {...props} />}
                        type="bottom"
                        className="page"
                        delay={1000}
                        interval={500}
                    > */}
                        {children}
                        {/* </QueueAnim> */}
                    </Row>
                </Col>
            </QueueAnim>
        </OverPack>
    );
}
