export default function Page1() {
    const children = articles.map((card, i) => (
        <Col
            key={i.toString()}
            md={{ span: 7 }}
            xs={24}
            className="help-card-wrapper sm:mt-10"
        >
            <img src={card.img} className="w-[77px]" />
            <p className="help-card-title mt-[8.5px]">{card.title}</p>
            <div className="help-card-detail">{card.description}</div>
            <a href={card.img} className="help-card-read-more mt-[20px]">
                Read more
                <ArrowRightOutlined />
            </a>
        </Col>
    ));

    return (
        <OverPack
            component="section"
            className={`page-help-wrapper min-h-[300px]`}
        >
            <QueueAnim
                component={Row}
                type="bottom"
                className="page row text-center page-help"
                leaveReverse={true}
                key="page-help"
            >
                <Col
                    className="trustby-heading mb-10 w-[672px] m-auto"
                    key="trustby-heading"
                >
                    We help your business grow faster.
                    <p className="help-info w-[80%] m-auto mt-[13px]">
                        Why kept very ever home mrs. Considered sympathize ten
                        uncommonly occasional assistance sufficient.
                    </p>
                </Col>
                <Col
                    span={24}
                    key="cards"
                    className="flex justify-between mb-[42px]"
                >
                    <Row justify="space-between">
                        {/* <QueueAnim
                        component={props => <Row type='flex' justify='space-between' {...props} />}
                        type="bottom"
                        className="page"
                        delay={1000}
                        interval={500}
                    > */}
                        {children}
                        {/* </QueueAnim> */}
                    </Row>
                </Col>
                <Col span={24} className="flex justify-center">
                    <button className="flex items-center justify-center more-about-platform py-5 px-12">
                        More About Platform
                    </button>
                </Col>
            </QueueAnim>
        </OverPack>
    );
}
