import React from "react";
import PropTypes from "prop-types";
import QueueAnim from "rc-queue-anim";
import { Row, Col } from "antd";
import { Element } from "rc-scroll-anim";
import "./info.scss";
import RcScrollOverPack from "rc-scroll-anim/lib/ScrollOverPack";

class Banner extends React.PureComponent {
    static propTypes = {
        className: PropTypes.string,
        isMobile: PropTypes.bool,
        navToShadow: PropTypes.func,
    };
    static defaultProps = {
        className: "page-info",
    };
    render() {
        const { className, isMobile, navToShadow } = this.props;
        return (
            <Element
                component="section"
                className={`${className}-wrapper page`}
                onChange={navToShadow}
            >
                <Row
                    type="flex"
                    justify={"space-between"}
                    className={`${className}`}
                    gutter={1}
                >
                    <RcScrollOverPack
                        component={(props) => <Col sm={10} md={7} {...props} />}
                    >
                        <QueueAnim
                            type={"left"}
                            className={`${className}-text-wrapper flex flex-col justify-center`}
                            leaveReverse
                            key={`${className}-text-wrapper`}
                        >
                            <section className="testimonials" key="section1">
                                Testimonials
                            </section>
                            <h1 key="h1" className="check-saying">
                                Check what our clients are saying
                            </h1>
                        </QueueAnim>
                    </RcScrollOverPack>
                    <RcScrollOverPack
                        component={(props) => (
                            <Col sm={12} md={15} {...props} />
                        )}
                    >
                        <QueueAnim
                            type={"bottom"}
                            className={`${className}-text-wrapper flex flex-col justify-center`}
                            leaveReverse
                            key={`${className}-text-wrapper`}
                        >
                            <img
                                src="/landing-page/quotes.svg"
                                key="quotes"
                                width="14px"
                            />
                            <section className="quotes-1" key="section1">
                                Since using this platform, our advertising game
                                has completely transformed. we would highly
                                recommend this platform to any business looking
                                to simplify their advertising process and drive
                                more conversions.
                            </section>
                            <section className="quotes-2" key="section2">
                                Adrian cal
                            </section>
                            <section className="quotes-3" key="section3">
                                CEO GetNextDesign
                            </section>
                        </QueueAnim>
                    </RcScrollOverPack>
                </Row>
            </Element>
        );
    }
}

export default Banner;
