import { Col, Row, Button } from "antd";
import QueueAnim from "rc-queue-anim";
import OverPack from "rc-scroll-anim/lib/ScrollOverPack";
import React from "react";
import "./final.scss";

export default function Page1() {
    return (
        <OverPack
            component="section"
            className={`page-help-wrapper min-h-[300px]`}
        >
            <QueueAnim
                component={Row}
                type="bottom"
                className="page row text-center page-help"
                leaveReverse={true}
                key="page-help"
            >
                <Col
                    className="trustby-heading mb-10 w-[672px] m-auto"
                    key="trustby-heading"
                >
                    Start optimizing your ad campaigns today
                    <p className="help-info w-[80%] m-auto mt-[13px]">
                        Ready to take your advertising to the next level? Sign
                        up for our platform today
                    </p>
                </Col>
                <Col span={24} className="flex justify-center mt-[42px]">
                    <Button className="button text-white bg-[#06226B] hover:bg-white hover:text-[#06226B] border-1 border-black rounded-[47px] hover:bg-transparent mx-3">
                        Get Started
                    </Button>
                    <Button className="button text-black  border-black bg-none ml-3 rounded-[47px] mx-3">
                        View Pricing
                    </Button>
                </Col>
            </QueueAnim>
        </OverPack>
    );
}
