import React from "react";
import DocumentTitle from "react-document-title";
import { enquireScreen } from "enquire-js";
import Header from "./Header";
import Banner1 from "./Banner1";
import Banner2 from "./Banner2";
import Banner3 from "./Banner3";
import Banner4 from "./Banner4";
import Banner5 from "./Banner5";
import PageTrustBy from "./PageTrustBy";
import PageHelp from "./PageHelp";
import PageInfo from "./PageInfo"
import PageFinal from "./PageFinal"
import Footer from "./Footer";

let isMobile = false;
enquireScreen((b) => {
    isMobile = b;
});

class Home extends React.PureComponent {
    state = {
        isMobile,
        showShadow: false,
    };

    componentDidMount() {
        enquireScreen((b) => {
            this.setState({
                isMobile: !!b,
            });
        });
    }
    navToShadow = (e) => {
        this.setState({ showShadow: e.mode === "leave" });
    };
    render() {
        return (
            <div className="bg-[url('../public/landing-page/background.png')] bg-cover bg-no-repeat">
                <Header
                    key="header"
                    className={this.state.showShadow ? "show-shadow" : ""}
                />
                ,
                <Banner1
                    key="banner1"
                    isMobile={this.state.isMobile}
                    navToShadow={this.navToShadow}
                />
                ,
                {/* <Banner key="banner" isMobile={this.state.isMobile} navToShadow={this.navToShadow} />, */}
                <PageTrustBy key="page-trustby" />
                <Banner2
                    key="banner2"
                    isMobile={this.state.isMobile}
                    navToShadow={this.navToShadow}
                />
                ,
                <Banner3
                    key="banner3"
                    isMobile={this.state.isMobile}
                    navToShadow={this.navToShadow}
                />
                ,
                <Banner4
                    key="banner4"
                    isMobile={this.state.isMobile}
                    navToShadow={this.navToShadow}
                />
                ,
                <Banner5
                    key="banner5"
                    isMobile={this.state.isMobile}
                    navToShadow={this.navToShadow}
                />
                ,
                <PageHelp key="page-help" />
                <PageInfo />
                <PageFinal />
                <Footer key="footer" />,
                <DocumentTitle title="Ant-V" />,
            </div>
        );
    }
}
export default Home;
